/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  background: #0a0a23;
  color: #ffffff;
}

/* Tutorial Container */
.tutorial-container {
  max-width: 1200px;
  margin: 60px auto 0; /* Account for fixed navbar */
  padding: 3rem 1rem;
  text-align: center;
  color: #ffffff;
}

/* Headings */
h1 {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: linear-gradient(90deg, #00c6ff, #0072ff);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

h2 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #00c6ff;
  margin-bottom: 1rem;
}

/* Tutorial Card */
.tutorial-card {
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 1.5rem;
  margin: 1.5rem 0;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.tutorial-card:hover {
  transform: scale(1.03);
  box-shadow: 0 0 15px rgba(0, 255, 255, 0.6);
}

/* Paragraphs */
p {
  font-size: 1.1rem;
  color: #dddddd;
  margin-bottom: 1rem;
}

/* Video Container */
.video-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  justify-content: center;
}

/* Iframe */
iframe {
  border-radius: 8px;
  width: 100%;
  max-width: 560px;
  height: 315px;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.6);
  transition: transform 0.3s ease-in-out;
}

iframe:hover, iframe:focus {
  transform: scale(1.05);
  outline: none;
}

/* Details/Summary for FAQs or Expandable Content */
details {
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 1rem;
  margin: 1rem 0;
}

summary {
  font-size: 1.1rem;
  cursor: pointer;
  color: #00c6ff;
  transition: color 0.3s ease;
}

details[open] summary {
  color: #0072ff;
}

summary:focus {
  outline: none;
  color: #0072ff;
}

/* Responsive Design */
@media (max-width: 768px) {
  .tutorial-container {
    max-width: 95%;
    padding: 2rem 0.5rem;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  p {
    font-size: 1rem;
  }

  iframe {
    max-width: 100%;
    height: 250px;
  }

  .video-container {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.8rem;
  }

  h2 {
    font-size: 1.3rem;
  }

  p {
    font-size: 0.95rem;
  }

  iframe {
    height: 200px;
  }

  .tutorial-card {
    padding: 1rem;
  }
}
.tutorial-container {
  padding: 20px;
  color: white;
  background-color: #121212;
}

.filter-bar {
  margin-bottom: 20px;
}

.tutorial-card {
  background: #1f1f1f;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 30px;
  box-shadow: 0 0 15px rgba(0,0,0,0.3);
}

.video-container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 10px;
}

iframe {
  flex: 1;
  min-width: 300px;
}
